body {
	background: url(../images/bg.png) repeat;
	background-attachment: fixed;
}

html,
body {
  height: 100%;
}

.container {
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	@media (min-width: 768px) {
		width: 750px;
	}
	@media (min-width: 992px) {
		width: 970px;
	}
	@media (min-width: 1200px) {
		width: 1170px;
	}
}

.row-line {
	margin-right: -15px;
    margin-left: -15px;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	&__between {
		justify-content: space-between;
	}
}

.hidden-sm {
	@media (max-width: 991px) and (min-width: 768px) {
		display: none !important;
	}
}
.hidden-xs {
	@media (max-width: 767px) {
		display: none !important;
	}
}

.hidden-md {
	@media (max-width: 1199px) and (min-width: 992px) {
		display: none !important;
	}
}

.hidden-lg {
	@media (min-width: 1200px) {
		display: none!important;
	}
}

.title {
	font-size: 36px;
	font-weight: bold;
	background: url(../images/border.png) bottom left no-repeat;
	padding-bottom: 10px;
	margin: 0 0 20px;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
}

h2, h3 {
    font-size: 22px;
    font-weight: bold;
}

h2, h3, h4, h5, h6 {
    color: #000;
    margin: 0 0 10px;
    padding: 10px 0;
}

h4, .h4 {
  font-size: 18px;
}

a {
	text-decoration: none;
	&:hover {
		text-decoration: underline;
	}
}

.wrapper {
  background: url(../images/bg.png) repeat;
  background-attachment: fixed;
  overflow: hidden;
  transition: transform .5s, -webkit-transform .5s;
}

root {
  display: block;
}

input, button, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
